<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('@/assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Documentos en línea</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Categoría > Subcategoría > Tipo de pago</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ operation.document.name }}</div>

          <div class="call-to-action text-center">
            <div>Costo del documento: S/ {{ operation.document_price }}</div>
            <div>Código de solicitud: {{ operation.id | padleft }}</div>
            <div>Abogado responsable: {{ operation.lawyer.name }}</div>
          </div>

          <coupon
              :price="operation.document_price"
              @code-used="onCodeUsed"
              @price-updated="onPriceUpdated"
          ></coupon>

          <p class="text-center">¿Cómo quieres pagar?</p>

          <div v-if="false" class="call-to-action" @click="goToPayment()">
            <div class="icon">
              <v-img :src="require('@/assets/icons/lawyer-05.svg')" contain height="24"/>
            </div>
            <div class="text">Pago con tarjeta</div>
          </div>

          <div class="call-to-action" @click="goToBank()">
            <div class="icon">
              <v-img :src="require('@/assets/icons/lawyer-06.svg')" contain height="24"/>
            </div>
            <div class="text">Depósito bancario</div>
          </div>

        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";
import axios from "axios";
import Coupon from '@/components/Coupon';

export default {
  name: 'DocumentVariablePaymentMethod',
  components: {Coupon},
  data: () => ({
    operation: {
      document: {},
      lawyer: {},
    },
    code: '',
  }),
  computed: {
    ...mapGetters(['user']),
    operationId() {
      return this.$route.params.id
    }
  },
  created() {
    this.loadOperation()
  },

  methods: {
    ...mapActions([]),
    loadOperation() {
      axios.get('operations/' + this.operationId).then((response) => {
        this.operation = response.data
      })
    },
    onCodeUsed(code) {
      this.code = code
    },
    onPriceUpdated(price) {
      this.priceTotal = price
    },
    goToPayment() {
      axios.put('operations/' + this.operationId, {coupon_code: this.code}).then(() => {
        this.$router.push({name: 'DocumentVariablePaymentOnline', params: {id: this.operationId}})
      })
    },
    goToBank() {
      axios.put('operations/' + this.operationId, {coupon_code: this.code}).then(() => {
        this.$router.push({name: 'DocumentVariablePaymentVoucher', params: {id: this.operationId}})
      })
    },
  }
}
</script>
